.call-window {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  z-index: -1;
  transition: opacity 0.5s;
  background: linear-gradient(top, #074055 0%, #030d10 100%); }
  .call-window.active {
    opacity: 1;
    z-index: 1; }
    .call-window.active .video-control {
      z-index: auto;
      -webkit-animation: in-fadeout 3s ease;
      animation: in-fadeout 3s ease; }
  .call-window .video-control {
    position: absolute;
    bottom: 20px;
    height: 72px;
    width: 100%;
    text-align: center;
    opacity: 0;
    z-index: -1;
    transition: opacity 0.5s; }
    .call-window .video-control:hover {
      opacity: 1; }
  .call-window video {
    position: absolute; }
  .call-window #localVideo {
    bottom: 0;
    right: 0;
    width: 20%;
    height: 20%;
    margin-right: 102px; }
  .call-window #peerVideo {
    width: 100%;
    height: 100%; }

@-webkit-keyframes in-fadeout {
  0% {
    opacity: 1; }
  75% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes in-fadeout {
  0% {
    opacity: 1; }
  75% {
    opacity: 1; }
  100% {
    opacity: 0; } }

.video-control,
.call-modal {
  margin-top: 245px; }
  .video-control .btn-action,
  .call-modal .btn-action {
    height: 50px;
