    line-height: 50px;
    margin: 0px 8px;
    font-size: 1.4em;
    text-align: center;
    border-radius: 50%;
    cursor: pointer;
    transition-duration: 0.25s; }
    .video-control .btn-action:hover,
    .call-modal .btn-action:hover {
      opacity: 0.8; }
    .video-control .btn-action.hangup,
    .call-modal .btn-action.hangup {
      background-color: #E81123;
      transform: rotate(135deg); }
    .video-control .btn-action:not(.hangup),
    .call-modal .btn-action:not(.hangup) {
      background-color: #00AFF0; }
      .video-control .btn-action:not(.hangup).disable,
      .call-modal .btn-action:not(.hangup).disable {
        background-color: #E81123; }

