.main-window {
  // padding-top: 40px;
  font-size: 1.75em;
  // background-color: yellow;

  @media screen and (max-width: 767px) {
    padding-top: 40px;

    .pull-left,
    .pull-right {
      width: 100%;
      text-align: center;
    }

    .pull-right {
      margin-top: 20px;
    }
  }

  .btn-action {
    $height: 60px;
    height: $height;
    width: $height;
    margin: 20px 30px 0px 0px;
    line-height: $height;
    text-align: center;
    border-radius: 50%;
    border: solid 2px #ffffff;
    cursor: pointer;
    transition-duration: 0.25s;
    background-color: transparent;

    &:hover {
      background-color: rgba(#ffffff, 0.2);
    }
  }

  .txt-clientId {
    height: 40px;
    margin: 40px auto 0px 10px;
    color: #ffffff;
    font-size: 0.9em;
    background-color: transparent;
    border: none;
    border-bottom: solid 1px #ffffff;
  }
}
